  <template>
  <div class="container chargebacks-content">
    <div class="row">
      <div class="col-sm-12">
        <div class="view-headline">
          <h2>About how many chargebacks<br> do you receive per month?</h2>
        </div>
        <div class="input-slider-box">
          <p class="validation-message hide">Enter a valid number</p>
          <div class="input-wrap">
            <span class="currency">$</span>
            <input
              type="number"
              class="input-text"
              min="1"
              v-model="scrubVal"
              v-on:keypress="isNumber"
              v-on:keydown="isNumber"
              pattern="\d*">
          </div>
          <Slider
            class="slider-comp"
            endCharacter="$ +"
            :startVal="scrubVal"
            :minVal="1"
            :maxVal="100000"
            :valOverride="inputVal"
            @on-scrub-val="getRangeVal" />
        </div>
      </div>
    </div> <!-- .row END -->

    <div class="row sales-method-row">
      <div class="col-sm-12">
      </div>
    </div> <!-- .row END -->
  </div> <!-- .container END -->
</template>

<script>
import { mapState } from 'vuex'
import Slider from '@/components/shared/slider'

export default {
  name: 'Chargebacks',
  components: {
    Slider
  },
  data () {
    return {
      setNextRoute: {
        set: 'chargeback-management',
        showNext: true
      },
      setPreviousRoute: 'chargebacks',
      routeProgress: 50,
      scrubVal: 1000
    }
  },
  computed: {
    ...mapState([
      'nextRoute',
      'progress'
    ]),
    inputVal: function () {
      return parseInt(this.scrubVal)
    }
  },
  methods: {
    next: function (e) {
      this.$router.push(this.nextRoute)
    },
    getRangeVal: function (val) {
      this.scrubVal = val
    },
    isNumber: function (evt) {
      var charCode = (evt.which) ? evt.which : evt.keyCode
      return charCode > 31 && (charCode < 48 || charCode > 57)
    },
    numberWithCommas: (x) => {
      return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',')
    }
  },
  mounted: function () {
    this.$store.dispatch('updateProgress', this.routeProgress)
    this.$store.dispatch('updateNextRoute', this.setNextRoute)
    this.$store.dispatch('updateRouteHistory', this.setPreviousRoute)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">

  @import '@/assets/scss/variables.scss';

  .chargebacks-route {
    .view-headline {
      br {
        display: block;
      }
    }
  }

  .input-wrap {
    position: relative;

    .currency {
      position: absolute;
      font-size: 21px;
      left: 12px;
      line-height: 50px;
      color: #c5c5c5;
    }

    .input-text {
      padding-left: 30px;
    }
  }

  .slider-comp {
    margin-top: 45px;
  }

  .input-slider-box {
    margin: 0 auto;
  }

  .validation-message {
    margin-bottom: 10px;
    color: #ff0000;
  }

  @media(min-width: 375px) {

    .input-slider-box {
      width: 300px;
    }

  }

  @media(min-width: 480px) {

    .input-slider-box {
      width: 400px;
    }

  }

  @media(min-width: 768px) {

    .chargebacks-route {
      .view-headline {
        br {
          display: none;
        }
      }
    }

  }

</style>



