<style lang="scss">

  @import '@/assets/scss/variables.scss';

  .header {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    padding-top: 5px;
    padding-bottom: 5px;
    border-bottom: 1px solid #E6E6E6;
    background-color: #fff;
