import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Product from '@/components/Product'
import Industry from '@/components/Industry'
import SalesMethod from '@/components/SalesMethod'
import Chargebacks from '@/components/Chargebacks'
import AverageTicket from '@/components/AverageTicket'
import ChargebackManagement from '@/components/ChargebackManagement'
import ChargebackWins from '@/components/ChargebackWins'
import v404 from '@/components/v404'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/product',
      name: 'product',
      component: Product
    },
    {
      path: '/industry',
      name: 'industry',
      component: Industry
    },
    {
      path: '/sales-method',
      name: 'sales-method',
      component: SalesMethod
    },
    {
      path: '/chargebacks',
      name: 'chargebacks',
      component: Chargebacks
    },
    {
      path: '/average-ticket',
      name: 'average-ticket',
      component: AverageTicket
    },
    {
      path: '/chargeback-management',
      name: 'chargeback-management',
      component: ChargebackManagement
    },
    {
      path: '/chargeback-wins',
      name: 'chargeback-wins',
      component: ChargebackWins
    },
    {
      path: '/404',
      name: 'v404',
      component: v404
    },
    {
      path: '*',
      redirect: '/404'
    }
  ]
})



