<template>
  <div id="app" :class="this.$route.name + '-route'">
    <Header />
    <section class="main-content">
      <Progress />
      <transition name="fade" mode="out-in">
        <router-view class="view-content" />
      </transition>
      <Navigation />
    </section>
    <Footer />
  </div>
</template>

<script>
import Progress from '@/components/shared/progress'
import Header from '@/components/shared/header'
import Footer from '@/components/shared/footer'
import Navigation from '@/components/shared/navigation'

export default {
  name: 'App',
  components: {
    Progress,
    Header,
    Footer,
    Navigation
  }
}
</script>

<style lang="scss">
  @import '@/assets/scss/reset.scss';
  @import '@/assets/scss/grid.scss';
  @import '@/assets/scss/helpers.scss';
  @import '@/assets/scss/variables.scss';

  html {
    background-color: #fff;
    width: 100%;
    height: 100%;
    overflow-x: hidden;
  }

  body {
    font-family: "Open Sans", Arial, Helvetica, sans-serif;
    width: 100%;
    min-height: 100%;
    color: $text-color;
    background: url('./assets/images/background.png') repeat-y 0 0;
    background-size: 1000px;
    overflow-x: hidden;
  }

  *,
  :after,
  :before {
    box-sizing: border-box;
  }

  h1,
  h2,
  h3 {
    font-family: inherit;
    font-weight: 600;
    color: $headline-color
  }

  h2 {
    text-align: center;
    font-size: 19px;
    margin-bottom: 35px;
    line-height: 1.5em;
  }

  a {
    text-decoration: none;

    &:hover {
        text-decoration: underline;
        cursor: pointer;
    }
  }

  p,
  li,
  a,
  button,
  span {
    font-family: inherit;
    color: $text-color;
  }

  button[disabled] {
    &:hover {
      background-color: $primary-color;
    }
  }

  #app {
    float: left;
    width: 100%;
  }

  .fade-enter-active, .fade-leave-active {
    transition: opacity .25s;
  }

  .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
  }

  .input-text {
    margin: 0 auto;
    display: block;
    width: 100%;
    height: 50px;
    font-size: 20px;
    padding-left: 20px;
    padding-right: 20px;
    border: 1px solid #e6e6e6;

    &:focus {
      border: 1px solid #afd35b;
    }
  }

  .main-content {
    margin-top: 47px;
    margin-bottom: 80px;
  }

  .primary-button {
    display: block;
    background-color: #AFD25C;
    color: #fff;
    text-align: center;
    font-size: 16px;
    font-weight: bold;
    transition: background-color 0.20s;
    padding-top: 25px;
    padding-bottom: 25px;

    &:hover,
    &:focus {
      outline: none;
      text-decoration: none;
      background-color: $primary-offset-bright;
    }
  }

  .view-content {
    padding-top: 80px;
    margin-bottom: 50px;
  }

  .view-headline {
    text-align: center;
    margin-bottom: 50px;

    h2 {
      display: inline;
      vertical-align: middle;
    }
  }

  /* Media Queries */

  @media(min-width: 375px) {
  }

  @media(min-width: 480px) {

    .main-content {
      margin-top: 85px;
    }

    h2 {
      font-size: 24px;
    }

    .view-headline {
      h2 br {
        display: none;
      }
    }
  }

  @media(min-width: 768px) {

    .view-content {
      padding-top: 137px;
      margin-bottom: 130px;
    }

  }

  @media(min-width: 992px) {

    body {
      background-size: 1440px;
    }

  }

  @media(max-height: 700px) and (min-width: 768px) {

    .view-content {
      padding-top: 100px;
    }

  }

</style>



