<template>
  <div class="container progress-container">
    <div class="row">
      <div class="col-sm-12">
        <div class="progress-box clearfix">
          <div class="progress-bg">
            <div class="progress-bar" :style="'width:' + progress.toString() + '%'"></div>
          </div>
          <div class="progress-percent">{{ progress + '%' }}</div>
        </div>
      </div>
    </div>
  </div> <!-- .container END -->
</template>

<script>
import { mapState } from 'vuex'

export default {
  name: 'Progress',
  computed: {
    ...mapState([
      'progress'
    ])
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
  @import '@/assets/scss/variables.scss';

  .progress-container {
    position: absolute;
    padding-top: 25px;
  }

  .progress-box {
    position: relative;
    width: 87%;
    margin-right: 40px;
  }

  .progress-bg {
    float: left;
    width: 100%;
    height: 13px;
    background-color: #f6f6f6;
    border: 1px solid #e6e6e6;
    border-radius: 10px;
  }

  .progress-bar {
    width: 0;
    height: 11px;
    background-color: $primary-color;
    border-radius: 10px;
    transition: all 0.5s;
  }

  .progress-percent {
    position: absolute;
    top: 0;
    right: -40px;
    color: $primary-color;
    font-size: 12px;
  }

  @media(min-width: 480px) {

    .progress-box {
      width: 350px;
      float: right;
    }

  }

  @media(min-width: 768px) {

    .progress-container {
      right: 10%;
      padding-top: 45px;
    }

  }

  @media(min-width: 1200px) {
  }

  @media(max-height: 700px) and (min-width: 1200px) {
  }

</style>



